#booking__container {
    margin: 25px;
    /* margin-top: calc(25px + 50px); */
    padding: 25px;
    background-color: rgb(227,55,0);
    color: white;
    border-radius: 6px;
    height: min-content;
    /* position: fixed; */
    box-shadow: rgba(149, 157, 165, 0.4) 0px 8px 24px;

}


#booking__title {
    margin-top: 0;
    text-align: center;
    border-bottom: 1px solid rgba(223, 223, 223, 0.258);
    padding-bottom: 15px;
}


#booking__container form{
    display: flex;
    flex-direction: column;
    gap: 25px;
    /* align-items: center; */
}

#description  {
    width: 100%;
    height: 150px;
    resize: none;
    border: none;
}

#description__container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    border-top: 1px solid rgba(223, 223, 223, 0.258);
    padding-top: 15px;
}

#booking__start-time {
    border-top: 1px solid rgba(223, 223, 223, 0.258);
    padding-top: 15px;
}

#booking__start-time, #booking__end-time {
    display: flex;
    gap: 25px;
    align-items: center;
    justify-content: space-between;
}

#booking__date {
    margin: auto;
    color: black;
}


#booking__button {
    background-color: rgb(82, 82, 169);
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    margin-top: 15px;
    transition: background-color .15s ease-in-out;

}

#booking__button:hover {
    background-color: rgb(60, 60, 148);

}



#booking__end-time select, #booking__start-time select {
    color: white;
    background-color: transparent;
}


#booking__end-time select option, #booking__start-time select option{
    color: black;
}

select.input-no-style {
    cursor: pointer;
}



.booking.error-container {
    font-style: italic;
    font-weight: 300;
}

.edit-booking.error-container {
    padding-top: 10px;
    color: red;

}

.error__list-item {
    padding: 3px 0;
}
