@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Josefin Sans', sans-serif;
}



body {
    max-width: 1300px;
    min-height: 75vh;
    margin: auto;
    background-color: rgb(247, 247, 247);
    color: black;

}


.body {
    background-color: rgb(255, 255, 255);
    min-height: calc(100vh - 250px);
    height: 100%;
    margin-top: 70px;

}

button:hover {
    cursor: pointer;
}

.blur {
    /* filter: blur(4px); */
}


.no-styling-button {
    background-color: transparent;
    border: none;
}


h1, h2, h3, h4, h5, h6 {
    margin: none;
}

#modal {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 2000;
}

#modal_background {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	/* background-color: rgba(255, 255, 255, 1); */
}

#modal_content {
	color: black;
	border-radius: 4px;
	/* overflow: scroll; */

	background-color: white;

	position: relative;
}

#modal_content::-webkit-scrollbar {
	color: transparent;
}


#modal_content.auth__modal{
	box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	height: 600px;
	width: 400px;
	background-color: rgb(39, 39, 39);
	border: 2px solid rgb(33, 33, 33);
	color: rgb(195, 195, 195);
}

#modal_content.booking__modal{
	box-shadow: rgba(0, 0, 0, 0.45) 0px 5px 15px;
	background-color: white;
	background-color: rgb(250, 250, 250);
	border: 2px solid rgb(231, 231, 231);
	height: 700px;
	width: 500px;
}

#modal_content.booking__modal select {
	color: black;
}

#post-image {
	cursor: pointer;
	box-shadow: none;
	transition: box-shadow .15s ease-in-out, -webkit-transform .2s ease-in-out;
	transition: box-shadow .15s ease-in-out, transform .2s ease-in-out;
	transition: box-shadow .15s ease-in-out, transform .2s ease-in-out, -webkit-transform .2s ease-in-out;
}

#post-image:hover {
	-webkit-transform: scale(1.004);
	        transform: scale(1.004);
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.auth__outer-container {
    width: 80%;
    padding: 25px 0;
    margin: auto;
}

.auth__title {
    font-weight: 500;
    font-size: 30px;
}

.auth__input {
    background-color: transparent;
    border: none;
    border-bottom: 1.5px solid rgb(103, 103, 194);
