}

.auth__input:focus {
    outline: none;
}

.auth__label {
    font-size: 13px;
    font-weight: 300;
    color: rgb(164, 164, 235);
    color: rgb(218, 218, 250);
}


.auth__input-container {
    display: flex;
    flex-direction: column;
    padding: 12px 0;
}


#auth__button {
    margin: 15px 0 25px 0;
